body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {    
    overflow: hidden;
    margin: 0;
    padding: 0;         
}

h1, h2, h3, h4, h5, input {
    font-family: 'Roboto', sans-serif;
}

.nav {
    display: flex;
    align-items: center;
    background: #0a540a;
    width: 100%;
    height: 50px;
}

/*Header*/
.title {
    color: white;
    font-weight: bold;
    font-size: 20px;
    font-family: sans-serif;
    margin-left: 10px;
}

.nav-itens {
    width: 86%;
    height: 46px;
}

.signout {
    color: white;
    font-size: 20px !important;        
    cursor: pointer;
}

.map {    
    width: 100%;
    height: 100%;
    position: "absolute";
}

.sidebar {    
    height: 100%;
    background-color: #272D39;
    position: absolute;
    top: 50px;
    right: 0;
}

.header-sidebar {
    height: 70px;
    border-bottom: 1px solid white;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.retry {
    color: white;
    cursor: pointer;
    margin-top: 5px;
    float: right;
    margin-right: 10px;
}

.cards {
    padding: 10px;
    overflow-y: auto;
    overflow-x: hidden;    
}

.card {        
    color: rgba(255, 255, 255, 0.65);
    padding: 0px 10px 0px 20px;;
    border: 1px solid rgba(255, 255, 255, 0.45);
    border-radius: 5px;
    margin-bottom: 10px;
}

input[type=text], select {    
    width: 70%;
    padding: 8px 15px;
    font-size: 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}
